export function sequenceArray<N extends number = number>(start: number, end: number) {
    const levels: number[] = []

    if (start <= end) {
        for (let i = start; i <= end; i++) levels.push(i)
    } else {
        for (let i = start; i >= end; i--) levels.push(i)
    }

    return levels as N[]
}
