import { MODULE_ID } from '@utils/module'
import { warn, NotifyArgs, info, error } from '@utils/foundry/notification'

export type LocalizeArgs = [key: string, data?: Record<string, string | number>]

export function localize(...args: LocalizeArgs) {
    let [key, data] = args
    key = `${MODULE_ID}.${key}`
    if (data) return game.i18n.format(key, data)
    return game.i18n.localize(key)
}

export function subLocalize(subKey: string) {
    const fn = (...args: LocalizeArgs) => localize(`${subKey}.${args[0]}`, args[1])

    Object.defineProperties(fn, {
        warn: {
            value: (...args: NotifyArgs) => warn(`${subKey}.${args[0]}`, args[1], args[2]),
            enumerable: false,
            configurable: false,
        },
        info: {
            value: (...args: NotifyArgs) => info(`${subKey}.${args[0]}`, args[1], args[2]),
            enumerable: false,
            configurable: false,
        },
        error: {
            value: (...args: NotifyArgs) => error(`${subKey}.${args[0]}`, args[1], args[2]),
            enumerable: false,
            configurable: false,
        },
    })

    return fn as ((...args: LocalizeArgs) => string) & {
        warn: (...args: NotifyArgs) => void
        info: (...args: NotifyArgs) => void
        error: (...args: NotifyArgs) => void
    }
}
