export function chatUUID(uuid: string, name?: string) {
    if (name) return `@UUID[${uuid}]{${name}}`
    return `@UUID[${uuid}]`
}

export function fakeChatUUID(name: string) {
    return `<span style="background: #DDD;
    padding: 1px 4px;
    border: 1px solid var(--color-border-dark-tertiary);
    border-radius: 2px;
    white-space: nowrap;
    word-break: break-all;">${name}</span>`
}
